<script>
  import { v4 as uuidv4 } from "uuid";
  import { compressToUTF16, decompressFromUTF16 } from "async-lz-string";
  import { createEventDispatcher } from "svelte";

  const dispatch = createEventDispatcher();
  let trade = {};
  trade.scrip = "";
  trade.spcredit = "";
  trade.bpdebit = "";

  $: if (trade.scrip != "") {
    let str = trade.scrip;
    trade.scrip = str.toUpperCase();
  }

  $: if (trade.spcredit >= -0.3 && trade.bpdebit >= -0.3) {
    trade.pnl = trade.spcredit - trade.bpdebit;
    trade.ntpnl = trade.pnl - trade.charges;
  }

  function validatePosition() {
    let uid = localStorage.getItem("uid");
    trade.oid = uuidv4();
    trade.quantity = trade.lots * trade.lotsize;
    fetch(
      `https://stockapi.mumbiakafestival.workers.dev/?type=addOptData&id=${uid}`,
      {
        method: "POST",
        mode: "no-cors",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(trade),
      }
    ).then(async (response) => {
      console.log(response);
      let storedTradesComp = localStorage.getItem("trades");
      if (storedTradesComp == " ") {
        let toPush = [];
        toPush.push(trade);
        const compressed = await compressToUTF16(JSON.stringify(toPush));
        localStorage.setItem("trades", compressed);
      } else {
        const decompressed = await decompressFromUTF16(storedTradesComp);
        let storedTrades = JSON.parse(decompressed);
        storedTrades.push(trade);
        const compressed = await compressToUTF16(JSON.stringify(storedTrades));
        localStorage.setItem("trades", compressed);
      }

      dispatch("message", {
        text: "added",
      });
      trade = {};
      trade.scrip = "";
      trade.spcredit = "";
      trade.bpdebit = "";
      trade.type = "PUT SELL";
      document.getElementById("closeModal").click();
    });
  }
</script>

<div
  class="modal modal-blur fade"
  id="modal-add"
  tabindex="-1"
  role="dialog"
  aria-hidden="true"
>
  <div class="modal-dialog modal-dialog-centered" role="document">
