          data-bs-dismiss="modal"
          aria-label="Close"
        />
      </div>
      <div class="modal-body">
        <div class="row">
          <div class="col-lg-6">
            <div class="mb-3">
              <span class="form-label">Scrip</span>
              <div class="input-group input-group-flat">
                <input
                  type="text"
                  bind:value={trade.scrip}
                  class="form-control"
                  placeholder="NIFTY"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-6">
            <div class="mb-3">
              <span class="form-label">Contract Open Date</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conopendt}
                />
              </div>
            </div>
          </div>

          <div class="col-lg-6">
            <div class="mb-3">
              <span class="form-label">Contract Expiration</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conexpndt}
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
              <span class="form-label">Lots</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.lots}
                  class="form-control"
                  placeholder="Lots"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
              <span class="form-label">Lot Size</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
