<script>
  import Dropzone from "svelte-file-dropzone";
  let files = [];
  let fileData = [];
  function processRawCSV(data) {
    const output = [];
    const rows = data.split("\n");
    for (let i = 0; i < rows.length; i++) {
      const cells = rows[i].split(",");
      output.push(cells);
    }
    return output;
  }
  function handleFilesSelect(e) {
    files = e.detail.acceptedFiles;
    for (let i = 0; i < files.length; i++) {
      const reader = new FileReader();
      reader.onload = () => {
        const binaryStr = reader.result;
        fileData = processRawCSV(binaryStr);
      };
      console.log(reader.readAsText(files[i]));
    }
  }
  function bulkAdd() {
    alert("Bulk Add");
  }
</script>

<div
  class="modal modal-blur fade"
  id="modal-bulk"
  tabindex="-1"
  role="dialog"
  aria-hidden="true"
>
  <div class="modal-dialog modal-dialog-centered" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Bulk add trades</h5>

        <button
          type="button"
          class="btn-close"
          id="closeModal4"
          data-bs-dismiss="modal"
          aria-label="Close"
        />
      </div>
      <div class="modal-body">
        <span class="modal-title">Supported Format - TSV,CSV</span><br />
        <span
          >Fields order - SCRIP,CONTRACT OPEN DATE,CONTRACT EXPIRATION,LOTS,LOT
          SIZE,QUANTITY,OPTION TYPE,STRIKE PRICE,SELL PRICE(CREDIT),BUY
          PRICE(DEBIT),CONTRACT CLOSE DATE,MARGIN BLOCKED,PNL,CHARGE,NET
          PNL,SPOT PRICE,CHANCE OF PROFIT,TAG,NOTES</span
        >
        <Dropzone
          on:drop={handleFilesSelect}
          multiple={false}
          accept=".csv,.tsv,."
        >
          <p>Drag 'n' drop supported files here, or click to select files</p>
        </Dropzone>
        {#each files as item}
          <h2>{item.name}</h2>
        {/each}

        <table border="1">
          {#each fileData as row}
            <tr>
              {#each row as item}
                <td>{item}</td>
              {/each}
            </tr>
          {/each}
        </table>
      </div>

      <!--Second Part-->

      <div class="modal-footer">
        <button class="btn btn-danger" data-bs-dismiss="modal"> Cancel </button>
        <button class="btn btn-primary ms-auto" on:click={bulkAdd}>
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
            ><path stroke="none" d="M0 0h24v24H0z" fill="none" /><line
              x1="12"
              y1="5"
              x2="12"
              y2="19"
            /><line x1="5" y1="12" x2="19" y2="12" /></svg
          >
          Bulk Add
        </button>
      </div>
    </div>
  </div>
</div>
