<script>
  import Addtrade from "./Addtrade.svelte";
  import ModifyTrade from "./ModifyTrade.svelte";
  import Bulkadd from "./Bulkadd.svelte";
  import { onMount } from "svelte";
  import { compressToUTF16, decompressFromUTF16 } from "async-lz-string";

  let tradeStr;
  function addProd() {
    setTimeout(() => {
      document.getElementById("add").click();
    }, 100);
  }
  let trades = [];
  function json(response) {
    return response.json();
  }
  async function refreshProd() {
    let uid = localStorage.getItem("uid");
    fetch(
      `https://stockapi.mumbiakafestival.workers.dev/?type=getAllOptData&id=${uid}`,
      {
        method: "GET",
      }
    )
      .then(json)
      .then(async function (data) {
        console.log("Request succeeded with JSON response", data);
        if (data.result) {
          trades = data.result;
        }

        const compressed = await compressToUTF16(JSON.stringify(trades));
        localStorage.setItem("trades", compressed);
      })
      .catch(function (error) {
        alert(error);
      });
    console.log("refresh");
  }

  onMount(async () => {
    refreshProd();
  });

  async function handleMessage(event) {
    if (event.detail.text == "added" || event.detail.text == "deleted") {
      let storedTradesComp = localStorage.getItem("trades");
      const decompressed = await decompressFromUTF16(storedTradesComp);
      trades = JSON.parse(decompressed);
    }
  }

  async function modifyTrade(trade) {
    setTimeout(() => {
      tradeStr = JSON.stringify(trade);

      document.getElementById("modify").click();
    }, 100);
  }

  async function bulk() {
    setTimeout(() => {
      document.getElementById("bulk").click();
    }, 100);
  }
</script>

<div class="col-12">
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">Trades</h3>
      <div class="ms-auto">
        <!--Refresh-->
        <button
          class="btn btn-outline-success d-none d-sm-inline-block"
          on:click={refreshProd}
        >
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon icon-tabler icon-tabler-refresh"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M20 11a8.1 8.1 0 0 0 -15.5 -2m-.5 -4v4h4" />
            <path d="M4 13a8.1 8.1 0 0 0 15.5 2m.5 4v-4h-4" />
          </svg>
          Refresh
        </button>
        <button
          data-bs-toggle="modal"
          id="add"
          data-bs-target="#modal-add"
          hidden
        />
        <button
          class="btn btn-outline-success d-sm-none btn-icon"
          on:click={refreshProd}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            oyt
            class="icon icon-tabler icon-tabler-refresh"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M20 11a8.1 8.1 0 0 0 -15.5 -2m-.5 -4v4h4" />
            <path d="M4 13a8.1 8.1 0 0 0 15.5 2m.5 4v-4h-4" />
          </svg>
        </button>

        <!--Bulk Import-->
        <button
          class="btn btn-outline-warning d-none d-sm-inline-block"
          on:click={bulk}
        >
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon icon-tabler icon-tabler-file-import"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M14 3v4a1 1 0 0 0 1 1h4" />
            <path
              d="M5 13v-8a2 2 0 0 1 2 -2h7l5 5v11a2 2 0 0 1 -2 2h-5.5m-9.5 -2h7m-3 -3l3 3l-3 3"
            />
          </svg>
          Bulk Add
        </button>
        <button
          data-bs-toggle="modal"
          id="bulk"
          data-bs-target="#modal-bulk"
          hidden
        />
        <button
          class="btn btn-outline-warning d-sm-none btn-icon"
          on:click={bulk}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon icon-tabler icon-tabler-file-import"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M14 3v4a1 1 0 0 0 1 1h4" />
            <path
              d="M5 13v-8a2 2 0 0 1 2 -2h7l5 5v11a2 2 0 0 1 -2 2h-5.5m-9.5 -2h7m-3 -3l3 3l-3 3"
            />
          </svg>
        </button>
        <!--Add -->
        <button
          class="btn btn-outline-primary d-none d-sm-inline-block"
          on:click={addProd}
        >
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
            ><path stroke="none" d="M0 0h24v24H0z" fill="none" /><line
              x1="12"
              y1="5"
              x2="12"
              y2="19"
            /><line x1="5" y1="12" x2="19" y2="12" /></svg
          >
          Add
        </button>
        <button
          data-bs-toggle="modal"
          id="add"
          data-bs-target="#modal-add"
          hidden
        />
        <button
          data-bs-toggle="modal"
          id="modify"
          data-bs-target="#modal-modify"
          hidden
        />
        <button
          class="btn btn-outline-primary d-sm-none btn-icon"
          on:click={addProd}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
            ><path stroke="none" d="M0 0h24v24H0z" fill="none" /><line
              x1="12"
              y1="5"
              x2="12"
              y2="19"
            /><line x1="5" y1="12" x2="19" y2="12" /></svg
          >
        </button>
      </div>
    </div>
    <div class="table-responsive mb-1">
      <table class="table card-table table-vcenter text-nowrap datatable">
        <thead>
          <tr>
            <th class="w-1">Scrip</th>
            <th>Contract Open Date</th>
            <th>Contract Expiration</th>
            <th>Lots</th>
            <th>Lot Size</th>
            <th>Quantity</th>
            <th>Option Type</th>
            <th>Strike Price</th>
            <th>Sell Price(Credit)</th>
            <th>Buy Price(Debit)</th>
            <th>Contract Close Date</th>
            <th>Margin Blocked</th>
            <th>PnL</th>
            <th>Charges</th>
            <th>Net PnL</th>
            <th>Spot Price</th>
            <th>Chance Of Profit</th>
            <th>Tag</th>
            <th>Notes</th>
          </tr>
        </thead>
        {#if trades.length >= 1}
          <tbody>
            {#each trades as trade, j}
              <tr on:click={modifyTrade(trade)}>
                <td>{trade.scrip}</td>
                <td>{trade.conopendt}</td>
                <td>{trade.conexpndt}</td>
                <td>{trade.lots}</td>
                <td>{trade.lotsize}</td>
                <td>{trade.quantity}</td>
                <td>{trade.type}</td>
                <td>{trade.strikeprice}</td>
                <td>{trade.spcredit}</td>
                <td>{trade.bpdebit}</td>
                <td>{trade.conclsdt}</td>
                <td>{trade.mrgblck}</td>
                <td>{trade.pnl}</td>
                <td>{trade.charges}</td>
                <td>{trade.ntpnl}</td>
                <td>{trade.sptprice}</td>
                <td>{trade.cop}%</td>
                <td>{trade.tag}</td>
                <td>{trade.notes}</td>
              </tr>
            {/each}
          </tbody>
        {/if}
      </table>
    </div>
  </div>
</div>

<svelte:component this={Addtrade} on:message={handleMessage} />
<svelte:component this={ModifyTrade} {tradeStr} on:message={handleMessage} />
<svelte:component this={Bulkadd} />

<style>
  tr:hover {
    background-color: #d1c1a9;
  }
</style>
