<script>
  import { v4 as uuidv4 } from "uuid";
  import { compressToUTF16, decompressFromUTF16 } from "async-lz-string";
  import { createEventDispatcher } from "svelte";

  export let tradeStr = 0;
  let trade = {};
  $: if (tradeStr != 0) {
    trade = JSON.parse(tradeStr);
  }

  trade.scrip = "";
  const dispatch = createEventDispatcher();

  $: if (trade.scrip != "") {
    let str = trade.scrip;
    trade.scrip = str.toUpperCase();
  }

  $: if (trade.spcredit >= -0.3 && trade.bpdebit >= -0.3) {
    trade.pnl = trade.spcredit - trade.bpdebit;
    trade.ntpnl = trade.pnl - trade.charges;
  }

  function deleteTrade() {
    let uid = localStorage.getItem("uid");
    fetch(
      `https://stockapi.mumbiakafestival.workers.dev/?type=deleteOptTrade&id=${uid}&oid=${trade.oid}`,
      {
        method: "GET",
      }
    )
      .then(json)
      .then(async function (data) {
        let storedTradesComp = localStorage.getItem("trades");
        if (storedTradesComp == " ") {
          alert("Something is wrong!!!");
        } else {
          const decompressed = await decompressFromUTF16(storedTradesComp);
          let storedTrades = JSON.parse(decompressed);
          for (var i = 0; i < storedTrades.length; i++) {
            if (storedTrades[i].oid == trade.oid) {
              storedTrades.splice(i, 1);
              const compressed = await compressToUTF16(
                JSON.stringify(storedTrades)
              );
              localStorage.setItem("trades", compressed);
            }
          }
        }

        dispatch("message", {
          text: "deleted",
        });
        console.log("deleted");
        document.getElementById("closeModal3").click();
      })
      .catch(function (error) {
        alert(error);
      });
  }
  function json(response) {
    return response.json();
  }
  function validatePosition() {
    let uid = localStorage.getItem("uid");
    trade.oid = uuidv4();
    trade.quantity = trade.lots * trade.lotsize;
    fetch(
      `https://stockapi.mumbiakafestival.workers.dev/?type=addOptData&id=${uid}`,
      {
        method: "POST",
        mode: "no-cors",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(trade),
      }
    ).then(async (response) => {
      console.log(response);
      let storedTradesComp = localStorage.getItem("trades");
      if (storedTradesComp == " ") {
        let toPush = [];
        toPush.push(trade);
        const compressed = await compressToUTF16(JSON.stringify(toPush));
        localStorage.setItem("trades", compressed);
      } else {
        const decompressed = await decompressFromUTF16(storedTradesComp);
        let storedTrades = JSON.parse(decompressed);
        storedTrades.push(trade);
        const compressed = await compressToUTF16(JSON.stringify(storedTrades));
        localStorage.setItem("trades", compressed);
      }

      dispatch("message", {
        text: "added",
      });
      trade = {};
      trade.scrip = "";
      trade.spcredit = "";
      trade.bpdebit = "";
      trade.type = "PUT SELL";
      document.getElementById("closeModal3").click();
    });
  }
</script>

<div
  class="modal modal-blur fade"
  id="modal-modify"
  tabindex="-1"
  role="dialog"
  aria-hidden="true"
>
  <div class="modal-dialog modal-dialog-centered" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Modify Trade</h5>
        <button
          class="btn btn-outline-danger d-none d-sm-inline-block"
          on:click={deleteTrade}
        >
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon icon-tabler icon-tabler-trash"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <line x1="4" y1="7" x2="20" y2="7" />
            <line x1="10" y1="11" x2="10" y2="17" />
            <line x1="14" y1="11" x2="14" y2="17" />
            <path d="M5 7l1 12a2 2 0 0 0 2 2h8a2 2 0 0 0 2 -2l1 -12" />
            <path d="M9 7v-3a1 1 0 0 1 1 -1h4a1 1 0 0 1 1 1v3" />
          </svg>
          Delete
        </button>
        <button
          class="btn btn-outline-danger d-sm-none btn-icon"
          on:click={deleteTrade}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon icon-tabler icon-tabler-trash"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <line x1="4" y1="7" x2="20" y2="7" />
            <line x1="10" y1="11" x2="10" y2="17" />
            <line x1="14" y1="11" x2="14" y2="17" />
            <path d="M5 7l1 12a2 2 0 0 0 2 2h8a2 2 0 0 0 2 -2l1 -12" />
            <path d="M9 7v-3a1 1 0 0 1 1 -1h4a1 1 0 0 1 1 1v3" />
          </svg>
        </button>
        <button
          type="button"
          class="btn-close"
          id="closeModal3"
          data-bs-dismiss="modal"
          aria-label="Close"
        />
      </div>
      <div class="modal-body">
        <div class="row">
          <div class="col-lg-6">
            <div class="mb-3">
              <span class="form-label">Scrip</span>
              <div class="input-group input-group-flat">
                <input
                  type="text"
                  bind:value={trade.scrip}
                  class="form-control"
                  placeholder="NIFTY"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-6">
            <div class="mb-3">
              <span class="form-label">Contract Open Date</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conopendt}
                />
              </div>
            </div>
          </div>

          <div class="col-lg-6">
            <div class="mb-3">
              <span class="form-label">Contract Expiration</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conexpndt}
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
